import React , {useEffect,useState} from "react";
import { Streamlit, withStreamlitConnection } from "streamlit-component-lib";
import './button.css'

const Button = (props) => {
  const[btnResponse,setBtnResponse] = useState(true);
  useEffect(() => Streamlit.setFrameHeight());
  function handleClick() {
    setBtnResponse(!btnResponse)
    Streamlit.setComponentValue(btnResponse)
  }
  return(
    <button type="submit" onClick={handleClick} className="hovertext" data-hover={props.args.hoverText}>
        <img src={props.args.file_path_url} height={props.args.height} alt="buttonpng" border="0" />
    </button>
  )
}
  
export default withStreamlitConnection(Button);